.buttonList {
    display: flex;
    margin: 0;
    padding: 0;
    margin-left: auto;
    margin-right: auto;
    list-style: none;
    justify-content: center;
}

.buttonBox {
    display: block;
    margin-right: 10px;
}

.button {
    background-color: #4caf50;
    border: none;
    border-radius: 8px;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
}

.button:hover {
    box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
        0 17px 50px 0 rgba(0, 0, 0, 0.19);
}